import Table from '../../mo/models';
import TableCell from '../../mo/models/tableCell';

export default function traverseAffectedRowCells(
  table: Table,
  targetRowIndex: number,
  callback: (rowIndex: number, colIndex: number, cell: TableCell) => void,
) {
  for (let rowIndex = 0; rowIndex < targetRowIndex; rowIndex += 1) {
    const row = table.nodes[rowIndex];
    for (let colIndex = 0; colIndex < row.nodes.length; colIndex += 1) {
      const cell = row.nodes[colIndex];
      if (cell) {
        const hidden = cell.data.hidden || false;
        const rowSpan = cell.data.rowSpan || 1;
        if (!hidden && rowIndex + rowSpan - 1 >= targetRowIndex) {
          callback(rowIndex, colIndex, cell);
        }
      }
    }
  }
}
